export type CommonErrorsTranslations = Partial<
  typeof import('../assets/locale/common-errors/common-errors_en.json')
>;

// `vite` imports JSON with `default` namespace
export type CommonErrorsTranslationsOrDefault =
  | (CommonErrorsTranslations & { default?: undefined })
  | { default: CommonErrorsTranslations };

export const resolveMessages = (
  messages: CommonErrorsTranslationsOrDefault,
): CommonErrorsTranslations =>
  typeof messages.default === 'object' ? messages.default : messages;
